import React, { FC } from 'react';
import './Rules.scss';
import selectCategoryImage from '../../images/rule_step_select-category.svg';
import selectCostImage from '../../images/rule_step_select-cost.svg';
import answerImage from '../../images/rule_step_answer.svg';
import rightImage from '../../images/rule_step_right.svg';
import wrongImage from '../../images/rule_step_wrong.svg';

const Rules: FC = () => {
  return (
    <section className='rules-section' id='rules'>
      <h2 className='rules-section__title'> </h2>
      <p className='rules-section__rule rules-section__rule_step_select-category'> 1  5  </p>
      <img className='rules-section__image rules-section__image_step_select-category' src={selectCategoryImage} alt=' ' />
      <p className='rules-section__rule rules-section__rule_step_select-cost'>  </p>
      <img className='rules-section__image rules-section__image_step_select-cost' src={selectCostImage} alt=' ' />
      <p className='rules-section__rule rules-section__rule_step_answer'>30  </p>
      <img className='rules-section__image rules-section__image_step_answer' src={answerImage} alt='  ' />
      <p className='rules-section__rule rules-section__rule_step_wrong'>     </p>
      <img className='rules-section__image rules-section__image_step_wrong' src={wrongImage} alt=' ' />
      <p className='rules-section__rule rules-section__rule_step_right'>     </p>
      <img className='rules-section__image rules-section__image_step_right' src={rightImage} alt=' !' />
      <div className='rules-section__path' />
    </section>
  );
};

export default Rules;
