import React, { FC } from 'react';
import './FinalPage.scss';
import Header from '../../components/Header/Header';
import finalEmoji from '../../images/it-game_final.svg';

const FinalPage: FC = () => {
  return (
    <div className='it-page'>
      <Header />
      <main className='it-page__main final'>
        <h1 className='final__title'> !</h1>
        <p className='final__subtitle'>
          ,  
          {' '}
          <span style={{ color: 'var(--color-text-accent)' }}></span>
          {' '}
          !
        </p>
        <img className='final__emoji' src={finalEmoji} alt='!' />
      </main>
    </div>
  );
};

export default FinalPage;
