import React, { FC } from 'react';
import './Header.scss';
import itGameLogo from '../../images/it-game-logo.svg';

const Header: FC = () => {
  return (
    <header className='header'>
      <img className='header__logo' src={itGameLogo} alt=' ' />
      <a className='header__link' href='#rules'> </a>
    </header>
  );
};

export default Header;
